@import "../../AppVariables.scss";
@import "../../Mixins.scss";

.menu__heading {
  display: flex;
  justify-content: center;
  &__h1 {
    // text-align: center;
    text-transform: uppercase;
    font-size: 5rem;
    color: $color-grey-dark-3;
  }
}

.menu {
  // height: 62rem;
  @include horizontalCenter;
  display: flex;
  justify-content: center;
  align-content: center;
  margin-top: 5rem;

  &__column {
    background-color: $color-grey-light-2;
    border-radius: 1rem;
    box-shadow: $shadow-dark;
    width: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0rem 5rem;

    &__inner-container {
      border-style: solid;
      border-radius: 1rem;
      border-color: $color-grey-dark-3;
      height: 90%;
      width: 90%;

      overflow: hidden;

      &__left {
        &__logo {
          height: 22rem;
          fill: $color-grey-light-2;
          background-color: $color-grey-dark-3;
          // border-top-right-radius: 0.85rem;
          // border-top-left-radius: 0.85rem;
          // border-radius: 0.85rem;
          overflow: hidden;
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          &__svg {
            height: 15rem;
          }
        }

        &__items {
          padding: 3rem 0rem;
          margin-bottom: 6rem;
          text-align: center;
          list-style: none;
          width: 80%;
          @include horizontalCenter;
        }
      }

      &__item {
        // text-align: center;
        display: flex;
        justify-content: space-between;
        // @include horizontalCenter();
        &__left {
          padding-bottom: 3rem;
          &__name {
            font-size: 2rem;
            text-align: left;
            text-transform: uppercase;
          }
          &__price {
            font-size: 1.5rem;
            text-align: left;
          }
          &__description {
            font-size: 1.2rem;
            text-align: left;
          }
        }

        &__right {
          display: grid;
          align-items: center;
          justify-content: center;
          padding-bottom: 3rem;

          &__form {
            display: flex;

            justify-content: end;
            gap: 1rem;

            &__input {
              width: 5rem;
              text-align: center;
              // color: $color-grey-light-3;
              background-color: $color-grey-light-2;
              border: none;
              border-bottom-style: solid;
              font-size: 2rem;
              &-label {
                text-align: end;
                align-self: flex-end;
                font-size: 2rem;
              }

              &:active,
              &:focus {
                border: none;
                border-bottom-style: solid;
                outline: none;
                border-bottom-color: #23a6d5;
              }
            }

            &__button {
              outline: none;
              border: none;
              cursor: pointer;
              background-color: $color-grey-dark-3;
              color: $color-grey-light-2;
              padding: 1.5rem 2rem;
              border-radius: 1rem;
              transition: all 0.2s;

              &:hover {
                @include hoverEffects();
              }
              &:active {
                @include hoverEffects();
                transform: translateY(1.5px);
              }

              &__span {
                pointer-events: none;
                color: inherit;
              }
            }
          }
        }
      }
    }
  }

  &__visible {
    @include visibleAnimation;
  }

  &__hidden {
    @include hiddenAnimation;
  }
}
